<template>
	<div class="addEdit-block">
		<el-form
			class="add-update-preview"
			ref="ruleForm"
			:model="ruleForm"
			:rules="rules"
			label-width="180px"
		>
			<template >
				<el-form-item class="input" v-if="type!='info'"  label="公告标题" prop="gonggaobiaoti" >
					<el-input v-model="ruleForm.gonggaobiaoti" placeholder="公告标题" clearable  :readonly="ro.gonggaobiaoti"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="公告标题" prop="gonggaobiaoti" >
					<el-input v-model="ruleForm.gonggaobiaoti" placeholder="公告标题" readonly></el-input>
				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="公告类型" prop="gonggaoleixing" >
					<el-input v-model="ruleForm.gonggaoleixing" placeholder="公告类型" clearable  :readonly="ro.gonggaoleixing"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="公告类型" prop="gonggaoleixing" >
					<el-input v-model="ruleForm.gonggaoleixing" placeholder="公告类型" readonly></el-input>
				</el-form-item>
				<el-form-item class="upload" v-if="type!='info' && !ro.gonggaotupian" label="公告图片" prop="gonggaotupian" >
					<file-upload
						tip="点击上传公告图片"
						action="file/upload"
						:limit="3"
						:multiple="true"
						:fileUrls="ruleForm.gonggaotupian?ruleForm.gonggaotupian:''"
						@change="gonggaotupianUploadChange"
					></file-upload>
				</el-form-item>
				<el-form-item class="upload" v-else-if="ruleForm.gonggaotupian" label="公告图片" prop="gonggaotupian" >
					<img v-if="ruleForm.gonggaotupian.substring(0,4)=='http'&&ruleForm.gonggaotupian.split(',w').length>1" class="upload-img" style="margin-right:20px;" v-bind:key="index" :src="ruleForm.gonggaotupian" width="100" height="100">
					<img v-else-if="ruleForm.gonggaotupian.substring(0,4)=='http'" class="upload-img" style="margin-right:20px;" v-bind:key="index" :src="ruleForm.gonggaotupian.split(',')[0]" width="100" height="100">
					<img v-else class="upload-img" style="margin-right:20px;" v-bind:key="index" v-for="(item,index) in ruleForm.gonggaotupian.split(',')" :src="$base.url+item" width="100" height="100">
				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="班级" prop="banji" >
					<el-input v-model="ruleForm.banji" placeholder="班级" clearable  :readonly="ro.banji"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="班级" prop="banji" >
					<el-input v-model="ruleForm.banji" placeholder="班级" readonly></el-input>
				</el-form-item>
				<el-form-item class="date" v-if="type!='info'" label="公告时间" prop="gonggaoshijian" >
					<el-date-picker
						value-format="yyyy-MM-dd HH:mm:ss"
						v-model="ruleForm.gonggaoshijian" 
						type="datetime"
						:readonly="ro.gonggaoshijian"
						placeholder="公告时间"
					></el-date-picker>
				</el-form-item>
				<el-form-item class="input" v-else-if="ruleForm.gonggaoshijian" label="公告时间" prop="gonggaoshijian" >
					<el-input v-model="ruleForm.gonggaoshijian" placeholder="公告时间" readonly></el-input>
				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="老师账号" prop="laoshizhanghao" >
					<el-input v-model="ruleForm.laoshizhanghao" placeholder="老师账号" clearable  :readonly="ro.laoshizhanghao"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="老师账号" prop="laoshizhanghao" >
					<el-input v-model="ruleForm.laoshizhanghao" placeholder="老师账号" readonly></el-input>
				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="老师姓名" prop="laoshixingming" >
					<el-input v-model="ruleForm.laoshixingming" placeholder="老师姓名" clearable  :readonly="ro.laoshixingming"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="老师姓名" prop="laoshixingming" >
					<el-input v-model="ruleForm.laoshixingming" placeholder="老师姓名" readonly></el-input>
				</el-form-item>
			</template>
			<el-form-item v-if="type!='info'"  label="公告内容" prop="gonggaoneirong" >
				<editor 
					style="min-width: 200px; max-width: 600px;"
					v-model="ruleForm.gonggaoneirong" 
					class="editor" 
					action="file/upload">
				</editor>
			</el-form-item>
			<el-form-item v-else-if="ruleForm.gonggaoneirong" label="公告内容" prop="gonggaoneirong" >
				<span class="text ql-snow ql-editor" v-html="ruleForm.gonggaoneirong"></span>
			</el-form-item>
			<el-form-item class="btn">
				<el-button class="btn3"  v-if="type!='info'" type="success" @click="onSubmit">
					<span class="icon iconfont icon-xihuan"></span>
					提交
				</el-button>
				<el-button class="btn4" v-if="type!='info'" type="success" @click="back()">
					<span class="icon iconfont icon-xihuan"></span>
					取消
				</el-button>
				<el-button class="btn5" v-if="type=='info'" type="success" @click="back()">
					<span class="icon iconfont icon-xihuan"></span>
					返回
				</el-button>
			</el-form-item>
		</el-form>
    

	</div>
</template>
<script>
	export default {
		data() {
			return {
				id: '',
				type: '',
			
			
				ro:{
					gonggaobiaoti : false,
					gonggaoleixing : false,
					gonggaotupian : false,
					banji : false,
					gonggaoneirong : false,
					gonggaoshijian : false,
					laoshizhanghao : false,
					laoshixingming : false,
				},
			
				ruleForm: {
					gonggaobiaoti: '',
					gonggaoleixing: '',
					gonggaotupian: '',
					banji: '',
					gonggaoneirong: '',
					gonggaoshijian: '',
					laoshizhanghao: '',
					laoshixingming: '',
				},

				rules: {
					gonggaobiaoti: [
						{ required: true, message: '公告标题不能为空', trigger: 'blur' },
					],
					gonggaoleixing: [
						{ required: true, message: '公告类型不能为空', trigger: 'blur' },
					],
					gonggaotupian: [
					],
					banji: [
					],
					gonggaoneirong: [
						{ required: true, message: '公告内容不能为空', trigger: 'blur' },
					],
					gonggaoshijian: [
					],
					laoshizhanghao: [
						{ required: true, message: '老师账号不能为空', trigger: 'blur' },
					],
					laoshixingming: [
					],
				},
			};
		},
		props: ["parent"],
		computed: {



		},
		components: {
		},
		created() {
			this.ruleForm.gonggaoshijian = this.getCurDateTime()
		},
		methods: {
			// 下载
			download(file){
				window.open(`${file}`)
			},
			// 初始化
			init(id,type) {
				if (id) {
					this.id = id;
					this.type = type;
				}
				if(this.type=='info'||this.type=='else'||this.type=='msg'){
					this.info(id);
				}else if(this.type=='logistics'){
					for(let x in this.ro) {
						this.ro[x] = true
					}
					this.logistics=false;
					this.info(id);
				}else if(this.type=='cross'){
					var obj = this.$storage.getObj('crossObj');
					for (var o in obj){
						if(o=='gonggaobiaoti'){
							this.ruleForm.gonggaobiaoti = obj[o];
							this.ro.gonggaobiaoti = true;
							continue;
						}
						if(o=='gonggaoleixing'){
							this.ruleForm.gonggaoleixing = obj[o];
							this.ro.gonggaoleixing = true;
							continue;
						}
						if(o=='gonggaotupian'){
							this.ruleForm.gonggaotupian = obj[o];
							this.ro.gonggaotupian = true;
							continue;
						}
						if(o=='banji'){
							this.ruleForm.banji = obj[o];
							this.ro.banji = true;
							continue;
						}
						if(o=='gonggaoneirong'){
							this.ruleForm.gonggaoneirong = obj[o];
							this.ro.gonggaoneirong = true;
							continue;
						}
						if(o=='gonggaoshijian'){
							this.ruleForm.gonggaoshijian = obj[o];
							this.ro.gonggaoshijian = true;
							continue;
						}
						if(o=='laoshizhanghao'){
							this.ruleForm.laoshizhanghao = obj[o];
							this.ro.laoshizhanghao = true;
							continue;
						}
						if(o=='laoshixingming'){
							this.ruleForm.laoshixingming = obj[o];
							this.ro.laoshixingming = true;
							continue;
						}
					}
				}
				// 获取用户信息
				this.$http({
					url: `${this.$storage.get('sessionTable')}/session`,
					method: "get"
				}).then(({ data }) => {
					if (data && data.code === 0) {
						var json = data.data;
						if(((json.laoshizhanghao!=''&&json.laoshizhanghao) || json.laoshizhanghao==0) && this.$storage.get("role")!="管理员"){
							this.ruleForm.laoshizhanghao = json.laoshizhanghao
							this.ro.laoshizhanghao = true;
						}
						if(((json.laoshixingming!=''&&json.laoshixingming) || json.laoshixingming==0) && this.$storage.get("role")!="管理员"){
							this.ruleForm.laoshixingming = json.laoshixingming
							this.ro.laoshixingming = true;
						}
					} else {
						this.$message.error(data.msg);
					}
				});
			
			},
			// 多级联动参数

			info(id) {
				this.$http({
					url: `tongzhigonggao/info/${id}`,
					method: "get"
				}).then(({ data }) => {
					if (data && data.code === 0) {
						this.ruleForm = data.data;
						//解决前台上传图片后台不显示的问题
						let reg=new RegExp('../../../upload','g')//g代表全部
						this.ruleForm.gonggaoneirong = this.ruleForm.gonggaoneirong.replace(reg,'../../../springboot3270a91w/upload');
					} else {
						this.$message.error(data.msg);
					}
				});
			},

			// 提交
			async onSubmit() {
					if(this.ruleForm.gonggaotupian!=null) {
						this.ruleForm.gonggaotupian = this.ruleForm.gonggaotupian.replace(new RegExp(this.$base.url,"g"),"");
					}
					var objcross = this.$storage.getObj('crossObj');
					await this.$refs["ruleForm"].validate(async valid => {
						if (valid) {
							if(this.type=='cross'){
								var statusColumnName = this.$storage.get('statusColumnName');
								var statusColumnValue = this.$storage.get('statusColumnValue');
								if(statusColumnName!='') {
									var obj = this.$storage.getObj('crossObj');
									if(statusColumnName && !statusColumnName.startsWith("[")) {
										for (var o in obj){
											if(o==statusColumnName){
												obj[o] = statusColumnValue;
											}
										}
										var table = this.$storage.get('crossTable');
										await this.$http({
											url: `${table}/update`,
											method: "post",
											data: obj
										}).then(({ data }) => {});
									}
								}
							}
							
							await this.$http({
								url: `tongzhigonggao/${!this.ruleForm.id ? "save" : "update"}`,
								method: "post",
								data: this.ruleForm
							}).then(async ({ data }) => {
								if (data && data.code === 0) {
									this.$message({
										message: "操作成功",
										type: "success",
										duration: 1500,
										onClose: () => {
											this.parent.showFlag = true;
											this.parent.addOrUpdateFlag = false;
											this.parent.tongzhigonggaoCrossAddOrUpdateFlag = false;
											this.parent.search();
											this.parent.contentStyleChange();
										}
									});
								} else {
									this.$message.error(data.msg);
								}
							});
						}
					});
			},
			// 获取uuid
			getUUID () {
				return new Date().getTime();
			},
			// 返回
			back() {
				this.parent.showFlag = true;
				this.parent.addOrUpdateFlag = false;
				this.parent.tongzhigonggaoCrossAddOrUpdateFlag = false;
				this.parent.contentStyleChange();
			},
			gonggaotupianUploadChange(fileUrls) {
				this.ruleForm.gonggaotupian = fileUrls;
			},
		}
	};
</script>
